@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css);

.links {
  color: white !important;
  text-decoration: none;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: rgb(69, 69, 69);
  color: white;
  height: 65px;
  font-family: "Lato", Arial, sans-serif;
  font-size: 1.3rem;
}

.reorder-button {
  display: none;
}

.title {
  padding-top: 15px;
  cursor: pointer;
}

.title span {
  padding-right: 5px;
}

.search {
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 7px;
  width: 350px;
  border-color: black;
  font-size: 15px;
}

.search-collapse {
  display: none;
}

input:focus {
  outline: none;
  border-width: 3px;
  border-color: #6394f8;
}

.login {
  padding-right: 43px;
  padding-top: 14px;
  cursor: pointer;
}

.logout-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.logout {
  cursor: pointer;
  margin: auto;
  font-size: 1.2rem;
}

.email {
  color: white !important;
  font-size: 1.2rem;
  text-decoration: none;
  padding: 5px;
}

.right-side-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 100%;
  width: 400px;
}

.login span {
  padding-left: 5px;
}

.shopping-cart-icon {
  margin-top: 17px;
}

.shopping-cart-icon li {
  display: inline;
  text-decoration: none;
  cursor: pointer;
}

.shopping-cart-icon li span {
  padding-left: 4px;
  padding-right: 4px;
}

ul :hover {
  color: #6394f8;
}

.badge {
  background-color: #6394f8;
  border-radius: 10px;
  color: white !important;
  display: inline-block;
  font-size: 13px;
  line-height: 1;
  padding: 3px 7px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
}

.container-hidden {
  display: none;
}

.container {
  margin: auto;
  width: 90%;
  position: absolute;
  top: 57px;
  font-size: 1rem;
}

.lighter-text {
  color: #abb0be;
}

.main-color-text {
  color: #6394f8;
}

.shopping-cart {
  margin: 20px 0;
  float: right;
  background: white;
  width: 320px;
  position: relative;
  border-radius: 4px;
  border-style: solid;
  border-color: black;
  border-width: 1px;
  padding: 20px;
}

.shopping-cart-header {
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 15px;
}

.modal-items-container {
  display: flex;
  flex-direction: column;
}

.single-modal-item {
  display: flex;
  flex-direction: row;
}

.single-modal-item img {
  width: 75px;
  height: 75px;
  object-fit: cover;
}

.single-modal-item-price {
  color: black;
  font-size: 1.1rem;
  padding: 20px;
  margin: auto;
}

.shopping-cart-total {
  float: right;
}

.shopping-cart-items {
  padding-top: 20px;
}

.shopping-cart-items li {
  margin-bottom: 18px;
}
.shopping-cart-items li img {
  float: left;
  margin-right: 12px;
}

.item-name {
  display: block;
  padding-top: 10px;
  font-size: 16px;
}

.item-price {
  color: #6394f8;
  margin-right: 8px;
}

.item-quantity {
  color: #abb0be;
}

.cart-icon {
  color: #515783;
  font-size: 24px;
  margin-right: 7px;
  float: left;
}

.modal-button {
  background: #6394f8;
  color: white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  display: block;
  border-radius: 3px;
  font-size: 16px;
  margin: 25px 0 15px 0;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

@media (max-width: 900px) {
  .logout-container {
    padding-left: 25px;
  }

  .title {
    font-size: 18px;
    width: 150px;
  }
  .search {
    width: 250px;
  }

  .login {
    padding-top: 15px;
    padding-right: 15px;
    padding-left: 8px;
    font-size: 18px;
  }

  .shopping-cart-icon {
    font-size: 18px;
    padding-right: 5px;
    padding-left: 5px;
  }
}

@media (max-width: 670px) {
  .header button {
    display: flex;
  }

  .title {
    padding-right: 1px;
  }

  .right-side-nav {
    display: none;
  }

  .right-side-nav-show {
    position: absolute;
    left: 0px;
    top: 60px;
    height: 100px;
    width: 100%;
    background-color: rgb(69, 69, 69);
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .reorder-button {
    display: flex;
    align-items: center;
    text-decoration: none;
    border: none;
    border-radius: 2px;
    font-size: 16px;
    height: 35px;
    padding: 5px;
    margin-top: 15px;
  }

  .shopping-cart {
    margin: 10px 0;
    float: right;
    background: white;
    width: 260px;
    position: relative;
    border-radius: 4px;
    border-style: solid;
    border-color: black;
    border-width: 1px;
    /* padding: 100px; */
  }
}
